@import '../../global/global.css';

:host {
  display: block;
}

/* Content area - reusable calendar content */
.date-picker-content {
  width: 100%;
  font-family: var(--font-inter);
  display: flex;
  flex-direction: column;
}

/* Month Navigation */
.month-navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: var(--spacing-sm);
  padding: var(--spacing-md);
}

/* Inline Month Picker - constrain to container */
.month-picker-inline {
  flex: 1;
  overflow: hidden;
  max-height: 270px;
  animation: fadeIn 150ms ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.month-picker-inline.animate-fade-out {
  animation: fadeOut 150ms ease-out;
}

@keyframes fadeOut {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(-10px);
  }
}

/* Calendar Grid */
.calendar-grid {
  padding: 0 var(--spacing-md) var(--spacing-md);
  flex: 1;
}

.day-names {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  width: 280px;
  gap: 0;
  margin-bottom: var(--spacing-sm);
}

.day-name {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: var(--font-inter);
  font-weight: var(--weight-medium);
  font-size: var(--text-sm-size);
  line-height: var(--text-sm-line);
  color: var(--color-neutral-500);
}

.date-cells {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  width: 280px;
  row-gap: var(--spacing-xs, 4px);
  column-gap: 0;
  justify-items: center;
  align-items: center;
}

/* Calendar Cell States */
.calendar-cell {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  border-radius: var(--radius-full);
  font-family: var(--font-inter);
  font-weight: var(--weight-medium);
  font-size: var(--text-sm-size);
  line-height: var(--text-sm-line);
  cursor: pointer;
  transition: all var(--animation-duration-fast, 0.1s) var(--animation-easing-ease, ease);
}

.calendar-cell.other-month .date-number {
  color: var(--color-neutral-400);
}

.calendar-cell.other-month.disabled .date-number {
  color: var(--color-neutral-300);
}

.calendar-cell.selectable .date-number {
  color: var(--color-neutral-500);
}

.calendar-cell.selectable:hover {
  background: var(--color-neutral-50);
}

.calendar-cell.selectable:hover .date-number {
  color: var(--color-neutral-800);
}

.calendar-cell.today {
  border: 2px solid var(--color-brand-600);
}

.calendar-cell.today .date-number {
  color: var(--color-neutral-500);
}

.calendar-cell.today:hover {
  background: var(--color-brand-100);
}

.calendar-cell.today:hover .date-number {
  color: var(--color-brand-600);
}

.calendar-cell.selected {
  background: var(--color-brand-600);
}

.calendar-cell.selected .date-number {
  color: var(--color-white);
  font-weight: var(--weight-semibold);
}

.calendar-cell.selected:hover {
  background: var(--color-brand-800);
}

.calendar-cell.selected:hover .date-number {
  color: var(--color-white);
  font-weight: var(--weight-semibold);
}

/* Range states - in-range background */
.calendar-cell.in-range {
  background: var(--color-neutral-100);
  border-radius: 0;
  position: relative;
}

.calendar-cell.in-range .date-number {
  color: var(--color-neutral-800);
  font-weight: var(--weight-medium);
}

.calendar-cell.in-range:hover {
  background: var(--color-neutral-100);
}

.calendar-cell.in-range:hover .date-number {
  background: var(--color-neutral-200);
  border-radius: var(--radius-full);
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Range start and end styling */
.calendar-cell.range-start,
.calendar-cell.range-end {
  position: relative;
}

.calendar-cell.range-start .date-number,
.calendar-cell.range-end .date-number {
  background: var(--color-brand-600);
  color: var(--color-white);
  font-weight: var(--weight-semibold);
  border-radius: var(--radius-full);
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.calendar-cell.range-start:hover .date-number,
.calendar-cell.range-start.selectable:hover .date-number,
.calendar-cell.range-end:hover .date-number,
.calendar-cell.range-end.selectable:hover .date-number {
  background: var(--color-brand-800);
  color: var(--color-white);
}

/* Range start - extend background to the right */
.calendar-cell.range-start {
  background: transparent;
}

.calendar-cell.range-start::after {
  content: '';
  position: absolute;
  left: 50%;
  top: 0;
  width: 50%;
  height: 100%;
  background: var(--color-neutral-100);
  border-radius: var(--radius-full) 0 0 var(--radius-full);
  z-index: 0;
}

.calendar-cell.range-start:hover::after,
.calendar-cell.range-start.selectable:hover::after {
  background: var(--color-neutral-200);
}

/* Range end - extend background to the left */
.calendar-cell.range-end {
  background: transparent;
}

.calendar-cell.range-end::after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 50%;
  height: 100%;
  background: var(--color-neutral-100);
  border-radius: 0 var(--radius-full) var(--radius-full) 0;
  z-index: 0;
}

.calendar-cell.range-end:hover::after,
.calendar-cell.range-end.selectable:hover::after {
  background: var(--color-neutral-200);
}

/* When both start and end are the same day */
.calendar-cell.range-start.range-end {
  background: transparent;
}

.calendar-cell.range-start.range-end::after {
  display: none;
}

.calendar-cell.range-start.range-end .date-number {
  border-radius: var(--radius-full);
}

/* Range start without end (first selection only) */
.calendar-cell.range-start:not(.in-range):not(:has(~ .range-end))::after {
  display: none;
}

/* Row start/end rounding for in-range cells */
.calendar-cell.in-range:nth-child(7n+1) {
  border-top-left-radius: var(--radius-full);
  border-bottom-left-radius: var(--radius-full);
}

.calendar-cell.in-range:nth-child(7n) {
  border-top-right-radius: var(--radius-full);
  border-bottom-right-radius: var(--radius-full);
}

/* Range start at end of row */
.calendar-cell.range-start:nth-child(7n)::after {
  display: none;
}

/* Range end at start of row */
.calendar-cell.range-end:nth-child(7n+1)::after {
  display: none;
}

/* Today in range */
.calendar-cell.today.in-range {
  background: var(--color-brand-50);
}

.calendar-cell.today.in-range .date-number {
  color: var(--color-brand-600);
  font-weight: var(--weight-medium);
  background: transparent;
  border: 2px solid var(--color-brand-600);
}

.calendar-cell.today.in-range:hover {
  background: var(--color-brand-100);
}

/* Today as range start/end */
.calendar-cell.today.range-start .date-number,
.calendar-cell.today.range-end .date-number {
  background: var(--color-brand-600);
  color: var(--color-white);
  border: none;
}

.calendar-cell.disabled {
  cursor: not-allowed;
}

.calendar-cell.disabled .date-number {
  color: var(--color-neutral-300);
}

.date-number {
  position: relative;
  z-index: 1;
}
