import { Component, Prop, State, Event, EventEmitter, Watch, h, Host } from '@stencil/core';
import { MonthYear } from '../bh-month-picker-content/bh-month-picker-content';

export interface SelectedDate {
  year: number;
  month: number; // 0-11
  day: number; // 1-31
}

export interface DisabledDate {
  year: number;
  month: number;
  day: number;
}

interface CalendarCell {
  day: number;
  isCurrentMonth: boolean;
  isToday: boolean;
  isSelected: boolean;
  isDisabled: boolean;
  isRangeStart: boolean;
  isRangeEnd: boolean;
  isInRange: boolean;
  date: Date;
}

@Component({
  tag: 'bh-date-picker-content',
  styleUrl: 'bh-date-picker-content.css',
  shadow: true,
})
export class BhDatePickerContent {
  /**
   * Minimum year in the picker
   */
  @Prop() minYear: number = 1998;

  /**
   * Maximum year in the picker
   */
  @Prop() maxYear: number = 2060;

  /**
   * Whether the picker is disabled
   */
  @Prop() disabled: boolean = false;

  /**
   * Array of disabled dates
   */
  @Prop() disabledDates: DisabledDate[] = [];

  /**
   * Initial month to display (0-11)
   */
  @Prop() initialMonth?: number;

  /**
   * Initial year to display
   */
  @Prop() initialYear?: number;

  /**
   * Display month (for external control, 0-11)
   */
  @Prop() displayMonth?: number;

  /**
   * Display year (for external control)
   */
  @Prop() displayYear?: number;

  /**
   * Range start date (for date range picker)
   */
  @Prop() rangeStart?: SelectedDate;

  /**
   * Range end date (for date range picker)
   */
  @Prop() rangeEnd?: SelectedDate;

  /**
   * Currently selected value
   */
  @Prop() value?: SelectedDate;

  /**
   * Whether to show days from other months
   */
  @Prop() showOtherMonthDays: boolean = true;

  @State() selectedDate: SelectedDate | null = null;
  @State() currentMonth: number = new Date().getMonth();
  @State() currentYear: number = new Date().getFullYear();
  @State() isMonthDropdownOpen: boolean = false;
  @State() isMonthDropdownClosing: boolean = false;

  private today = new Date();
  private monthNames = [
    'January', 'February', 'March', 'April', 'May', 'June',
    'July', 'August', 'September', 'October', 'November', 'December',
  ];
  private dayNames = ['Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa', 'Su'];

  /**
   * Event emitted when value changes
   */
  @Event({ bubbles: true, composed: true }) bhChange!: EventEmitter<SelectedDate | null>;

  /**
   * Event emitted when a date is selected
   */
  @Event({ bubbles: true, composed: true }) bhDateSelect!: EventEmitter<SelectedDate | null>;

  /**
   * Event emitted when month changes
   */
  @Event({ bubbles: true, composed: true }) bhMonthChange!: EventEmitter<{ month: number; year: number }>;

  /**
   * Event emitted when month/year changes
   */
  @Event({ bubbles: true, composed: true }) bhMonthYearChange!: EventEmitter<{ month: number; year: number }>;

  @Watch('value')
  handleValueChange(newValue: SelectedDate | undefined) {
    if (newValue) {
      this.selectedDate = newValue;
      if (this.displayMonth === undefined && this.displayYear === undefined) {
        this.currentMonth = newValue.month;
        this.currentYear = newValue.year;
      }
    } else {
      this.selectedDate = null;
    }
  }

  @Watch('displayMonth')
  handleDisplayMonthChange(newValue: number | undefined) {
    if (newValue !== undefined) {
      this.currentMonth = newValue;
    }
  }

  @Watch('displayYear')
  handleDisplayYearChange(newValue: number | undefined) {
    if (newValue !== undefined) {
      this.currentYear = newValue;
    }
  }

  componentWillLoad() {
    // Initialize month/year
    if (this.displayMonth !== undefined) {
      this.currentMonth = this.displayMonth;
    } else if (this.initialMonth !== undefined) {
      this.currentMonth = this.initialMonth;
    } else {
      this.currentMonth = this.today.getMonth();
    }

    if (this.displayYear !== undefined) {
      this.currentYear = this.displayYear;
    } else if (this.initialYear !== undefined) {
      this.currentYear = this.initialYear;
    } else {
      this.currentYear = this.today.getFullYear();
    }

    // Initialize selected date
    if (this.value) {
      this.selectedDate = this.value;
    }
  }

  private get isInRangeMode(): boolean {
    return this.rangeStart !== undefined || this.rangeEnd !== undefined;
  }

  private get calendarCells(): CalendarCell[] {
    const cells: CalendarCell[] = [];
    const firstDay = new Date(this.currentYear, this.currentMonth, 1);
    const lastDay = new Date(this.currentYear, this.currentMonth + 1, 0);

    // Get the day of the week for the first day (0 = Sunday, 6 = Saturday)
    let firstDayOfWeek = firstDay.getDay();
    // Convert to Monday start (0 = Monday, 6 = Sunday)
    firstDayOfWeek = firstDayOfWeek === 0 ? 6 : firstDayOfWeek - 1;

    // Add cells for days before the first of the month
    if (this.showOtherMonthDays) {
      for (let i = 0; i < firstDayOfWeek; i++) {
        const prevMonthLastDay = new Date(this.currentYear, this.currentMonth, -i);
        cells.unshift(this.createCell(prevMonthLastDay, false));
      }
    } else {
      for (let i = 0; i < firstDayOfWeek; i++) {
        cells.unshift({
          day: 0,
          isCurrentMonth: false,
          isToday: false,
          isSelected: false,
          isDisabled: true,
          isRangeStart: false,
          isRangeEnd: false,
          isInRange: false,
          date: new Date(this.currentYear, this.currentMonth, -i),
        });
      }
    }

    // Add cells for current month
    for (let day = 1; day <= lastDay.getDate(); day++) {
      const date = new Date(this.currentYear, this.currentMonth, day);
      cells.push(this.createCell(date, true));
    }

    // Add next month days to fill the grid
    if (this.showOtherMonthDays) {
      const totalCells = cells.length;
      const remainingCells = 42 - totalCells;

      for (let day = 1; day <= remainingCells; day++) {
        const nextMonthDay = new Date(this.currentYear, this.currentMonth + 1, day);
        cells.push(this.createCell(nextMonthDay, false));

        if (cells.length % 7 === 0) break;
      }
    }

    return cells;
  }

  private createCell(date: Date, isCurrentMonth: boolean): CalendarCell {
    const isRangeStart = this.isDateRangeStart(date);
    const isRangeEnd = this.isDateRangeEnd(date);
    const isInRange = this.isDateInRange(date);

    return {
      day: date.getDate(),
      isCurrentMonth,
      isToday: this.isSameDay(date, this.today),
      isSelected: this.isDateSelected(date),
      isDisabled: !isCurrentMonth || this.isDateDisabled(date),
      isRangeStart,
      isRangeEnd,
      isInRange,
      date,
    };
  }

  private isSameDay(date1: Date, date2: Date): boolean {
    return (
      date1.getFullYear() === date2.getFullYear() &&
      date1.getMonth() === date2.getMonth() &&
      date1.getDate() === date2.getDate()
    );
  }

  private isDateDisabled(date: Date): boolean {
    if (!this.disabledDates || !Array.isArray(this.disabledDates)) {
      return false;
    }
    const year = date.getFullYear();
    const month = date.getMonth();
    const day = date.getDate();
    return this.disabledDates.some(
      disabled => disabled.year === year && disabled.month === month && disabled.day === day
    );
  }

  private isDateSelected(date: Date): boolean {
    if (this.isInRangeMode) return false;
    if (!this.selectedDate) return false;

    return (
      date.getFullYear() === this.selectedDate.year &&
      date.getMonth() === this.selectedDate.month &&
      date.getDate() === this.selectedDate.day
    );
  }

  private isDateRangeStart(date: Date): boolean {
    if (!this.rangeStart) return false;

    return (
      date.getFullYear() === this.rangeStart.year &&
      date.getMonth() === this.rangeStart.month &&
      date.getDate() === this.rangeStart.day
    );
  }

  private isDateRangeEnd(date: Date): boolean {
    if (!this.rangeEnd) return false;

    return (
      date.getFullYear() === this.rangeEnd.year &&
      date.getMonth() === this.rangeEnd.month &&
      date.getDate() === this.rangeEnd.day
    );
  }

  private isDateInRange(date: Date): boolean {
    if (!this.rangeStart || !this.rangeEnd) return false;

    const dateTime = date.getTime();
    const startTime = new Date(this.rangeStart.year, this.rangeStart.month, this.rangeStart.day).getTime();
    const endTime = new Date(this.rangeEnd.year, this.rangeEnd.month, this.rangeEnd.day).getTime();

    return dateTime > startTime && dateTime < endTime;
  }

  private handleCellClick(cell: CalendarCell) {
    if (cell.isDisabled || this.disabled || !cell.isCurrentMonth) {
      return;
    }

    const newSelectedDate: SelectedDate = {
      year: cell.date.getFullYear(),
      month: cell.date.getMonth(),
      day: cell.date.getDate(),
    };

    // If we're in range mode, let the parent handle it
    if (this.isInRangeMode) {
      this.bhChange.emit(newSelectedDate);
      this.bhDateSelect.emit(newSelectedDate);
      return;
    }

    // Toggle selection
    const isCurrentlySelected = this.selectedDate &&
      this.selectedDate.year === newSelectedDate.year &&
      this.selectedDate.month === newSelectedDate.month &&
      this.selectedDate.day === newSelectedDate.day;

    if (isCurrentlySelected) {
      this.selectedDate = null;
      this.bhChange.emit(null);
      this.bhDateSelect.emit(null);
    } else {
      this.selectedDate = newSelectedDate;
      this.bhChange.emit(newSelectedDate);
      this.bhDateSelect.emit(newSelectedDate);
    }
  }

  private handleMonthDropdownToggle = () => {
    if (this.isMonthDropdownOpen) {
      this.closeMonthDropdown();
    } else {
      this.isMonthDropdownOpen = true;
    }
  };

  private closeMonthDropdown() {
    this.isMonthDropdownClosing = true;
    setTimeout(() => {
      this.isMonthDropdownOpen = false;
      this.isMonthDropdownClosing = false;
    }, 150);
  }

  private handleMonthYearSelect = (event: CustomEvent<MonthYear>) => {
    this.closeMonthDropdown();

    setTimeout(() => {
      const monthYear = event.detail;
      this.currentMonth = monthYear.month;
      this.currentYear = monthYear.year;
      this.bhMonthChange.emit({ month: monthYear.month, year: monthYear.year });
      this.bhMonthYearChange.emit({ month: monthYear.month, year: monthYear.year });
    }, 100);
  };

  private handlePreviousMonth = () => {
    if (this.disabled) return;

    let newMonth = this.currentMonth - 1;
    let newYear = this.currentYear;

    if (newMonth < 0) {
      newMonth = 11;
      newYear = this.currentYear - 1;
    }

    if (newYear >= this.minYear) {
      this.currentMonth = newMonth;
      this.currentYear = newYear;
      this.bhMonthChange.emit({ month: newMonth, year: newYear });
      this.bhMonthYearChange.emit({ month: newMonth, year: newYear });
    }
  };

  private handleNextMonth = () => {
    if (this.disabled) return;

    let newMonth = this.currentMonth + 1;
    let newYear = this.currentYear;

    if (newMonth > 11) {
      newMonth = 0;
      newYear = this.currentYear + 1;
    }

    if (newYear <= this.maxYear) {
      this.currentMonth = newMonth;
      this.currentYear = newYear;
      this.bhMonthChange.emit({ month: newMonth, year: newYear });
      this.bhMonthYearChange.emit({ month: newMonth, year: newYear });
    }
  };

  private getCellClass(cell: CalendarCell): string {
    const classes = ['calendar-cell'];

    if (!cell.isCurrentMonth) classes.push('other-month');
    if (cell.isToday) classes.push('today');
    if (cell.isRangeStart) classes.push('range-start');
    if (cell.isRangeEnd) classes.push('range-end');
    if (cell.isInRange) classes.push('in-range');
    if (cell.isSelected) classes.push('selected');
    if (cell.isDisabled) classes.push('disabled');
    else if (cell.isCurrentMonth) classes.push('selectable');

    return classes.join(' ');
  }

  render() {
    const currentMonthYear = `${this.monthNames[this.currentMonth]} ${this.currentYear}`;

    return (
      <Host>
        <div class="date-picker-content">
          {/* Month Navigation */}
          <div class="month-navigation">
            <bh-button-icon
              hierarchy="tertiary"
              size="md"
              iconName="chevron_left"
              onBhClick={this.handlePreviousMonth}
              disabled={this.disabled || (this.currentYear === this.minYear && this.currentMonth === 0)}
            ></bh-button-icon>

            <bh-button
              hierarchy="tertiary"
              size="sm"
              label={currentMonthYear}
              icon="trailing"
              iconName="expand_more"
              onBhClick={this.handleMonthDropdownToggle}
            ></bh-button>

            <bh-button-icon
              hierarchy="tertiary"
              size="md"
              iconName="chevron_right"
              onBhClick={this.handleNextMonth}
              disabled={this.disabled || (this.currentYear === this.maxYear && this.currentMonth === 11)}
            ></bh-button-icon>
          </div>

          {/* Calendar Grid or Month Picker */}
          {!this.isMonthDropdownOpen && (
            <div class="calendar-grid">
              {/* Day Names Header */}
              <div class="day-names">
                {this.dayNames.map(dayName => (
                  <div key={dayName} class="day-name">{dayName}</div>
                ))}
              </div>

              {/* Date Cells */}
              <div class="date-cells">
                {this.calendarCells.map((cell, index) => (
                  <div
                    key={index}
                    class={this.getCellClass(cell)}
                    onClick={() => this.handleCellClick(cell)}
                  >
                    <span class="date-number">{cell.day === 0 ? '' : cell.day}</span>
                  </div>
                ))}
              </div>
            </div>
          )}

          {/* Month Picker Content */}
          {(this.isMonthDropdownOpen || this.isMonthDropdownClosing) && (
            <div class={`month-picker-inline ${this.isMonthDropdownClosing ? 'animate-fade-out' : ''}`}>
              <bh-month-picker-content
                scrollable={true}
                height="275px"
                selectedMonth={this.currentMonth}
                selectedYear={this.currentYear}
                onBhChange={this.handleMonthYearSelect}
                minYear={this.minYear}
                maxYear={this.maxYear}
              ></bh-month-picker-content>
            </div>
          )}
        </div>
      </Host>
    );
  }
}
